import Engine from './utils/engine';

let totalTime = 30;

const app = new Engine(document.getElementById('canvas'), {
  debug: false,
  width: 480,
  height: 480
});

let delta = (frame, totalFrames) => {
  let t = (frame % (totalFrames + 1)) / totalFrames;
  return t;
}

let easing = (t) => {
  return t * t * t * t;
}

let drawDot = (ctx, x, y, size, color = 'red') => {
  ctx.save();
  ctx.beginPath();
  ctx.strokeStyle = color;
  ctx.arc(x, y, size, 0, Math.PI * 2);
  ctx.stroke();
  ctx.restore();
}

let getColor = (t, divide = 45, offset = 320) => {
  return `hsl(${ (t * divide) + offset }, 100%, 50%)`;
}

app.onRender((ctx, { frame, width, height, debug }) => {
  // ctx.clearRect(0, 0, width, height);

  if (frame <= totalTime) {
    ctx.save();
    ctx.scale(0.9, 0.9);
    ctx.translate(width * 0.05, width * 0.05);

    let startRadius = width / 50;
    let endRadius = width / 2;

    let radius = endRadius - startRadius;

    let startPos = startRadius;
    let endPos = width / 2;// - startPos;

    let pos = endPos - startPos;

    let sizeT = delta(frame, totalTime);
    let posT = delta(frame, totalTime);

    let poser = (t) => (easing(t) * pos) + startPos;
    let sizer = (t) => (easing(t) * radius) + startPos;

    // ctx.strokeStyle = '#f44336';
    ctx.strokeStyle = getColor(posT);
    ctx.beginPath();
    ctx.arc(
      poser(posT),
      poser(posT),
      sizer(sizeT),
      0, Math.PI * 2
    );
    // ctx.strokeRect(0, 0, sizer(sizeT) * 2, sizer(sizeT) * 2);
    ctx.stroke();

    if (debug) {
      drawDot(ctx, poser(0.5), poser(0.5), sizer(0.5));
      drawDot(ctx, poser(0), poser(0), sizer(0), 'blue');
      drawDot(ctx, poser(1), poser(1), sizer(1), 'green');
      drawDot(ctx, poser(0.5), poser(0.5), 5);
      drawDot(ctx, poser(0), poser(0), 5, 'blue');
      drawDot(ctx, poser(1), poser(1), 5, 'green');
    }

    // ctx.strokeRect(0, 0, width, height);
    ctx.restore();
  }
});

app.start();
